import { Type } from "@angular/core";

export interface OverlayMetadata {
    /** ID to register the overlay as on the service */
    id: string;
    /** Component to render into the overlay */
    component: Type<any>;
    /** Data to pass the component */
    data?: any;
}

/** List of Overlays to register on application initialisation */
export const OVERLAY_REGISTER: OverlayMetadata[] = [];
