import { OverlayContentComponent } from '@acaprojects/ngx-widgets';
import { Component } from '@angular/core';
import { OVERLAY_REGISTER } from '../../shared/globals/overlay-register';

@Component({
    selector: 'booking-rules-modal',
    templateUrl: './booking-rules-modal.template.html',
    styleUrls: ['./booking-rules-modal.styles.scss'],
})
export class BookingRulesModalComponent extends OverlayContentComponent {
    public model: any = {};

    /** Name of the set building */
    public get location(): string {
        const building = this.model.building || {};
        return building.name || '';
    }

    /** Booking rule details */
    public get rules(): string[] {
        const building = this.model.building || {};
        const details = building.booking_details || {};
        return details.rules || [];
    }

    /** Booking rule details */
    public get info(): string {
        const building = this.model.building || {};
        const details = building.booking_details || {};
        return details.info || '';
    }

    /** Booking map rule details */
    public get map_rules(): string[] {
        const building = this.model.building || {};
        const details = building.booking_details || {};
        return details.map_rules || [];
    }

    /** Other special booking rules */
    public get other_rules(): { title: string, rules: string[] }[] {
        const building = this.model.building || {};
        const details = building.booking_details || {};
        const rules = details.other_rules || {};
        return Object.keys(rules).reduce((a, v) => { a.push({ title: v, rules: rules[v] || [] }); return a; }, []);
    }

    public get contact(): string {
        const building = this.model.building || {};
        const details = building.booking_details || {};
        return details.contact || '';
    }

    public init() {
        if (!this.model.building) {
            this.close();
        }
    }

    public close() {
        setTimeout(() => this.fn.close(), 300);
    }

    public event(name: string) {
        setTimeout(() => this.fn.event(name), 300);
    }
}

OVERLAY_REGISTER.push({ id: 'booking-rules', component: BookingRulesModalComponent, data: {} });

