
import { Component, OnDestroy, Output, EventEmitter } from '@angular/core';
import { OverlayContentComponent } from '@acaprojects/ngx-widgets';
import { ADynamicFormField } from '@acaprojects/ngx-dynamic-forms';

import { AppService } from '../../services/app.service';
import { IBooking, IBookingNote } from '../../services/data/bookings.service';
import { generateBookingFormMetadata, rulesForSpace } from '../../shared/utilities/booking.utilities';
import { IUser } from '../../services/data/users.service';
import { formatSpaces } from '../../shared/utilities/formatting.utilities';
import { IRoom } from '../../services/data/rooms.service';
import { ICateringOrder } from '../../shell/booking/main-flow/catering/catering-order/catering-order.component';

import * as moment from 'moment';
import * as dayjs from 'dayjs';
import { BookingDetailsModalComponent } from '../../shell/booking/overlays/booking-details/booking-details.component';
import { BookingEquipmentDetailsModalComponent } from '../../shell/booking/overlays/equipment-details/equipment-details.component';
import { OVERLAY_REGISTER } from '../../shared/globals/overlay-register';

@Component({
    selector: 'meeting-details-overlay',
    templateUrl: './meeting-details-overlay.template.html',
    styleUrls: ['./meeting-details-overlay.styles.scss']
})

export class MeetingDetailsOverlayComponent extends OverlayContentComponent<AppService> implements OnDestroy {

    @Output() public changeBookingDetails = new EventEmitter();
    public module: any = null;

    /** Whether booking organiser is a delegate of the current user */
    public is_delegate: boolean;
    /** Email address of the delegate */
    public delegate: string;
    /** Checkin state of the booking */
    public checkin: 'unavailable' | 'available' | 'started' | 'expired';
    /** List of fields available for editing the booking */
    public form_fields: ADynamicFormField[];
    /** Duration to extend the booking by on update */
    public extend_by: number = 0;

    /** Whether the form fields are valid */
    public get valid(): boolean {
        this.form_fields.forEach(i => i.control.markAsDirty());
        return this.form_fields.reduce((a, v) => {
            const valid = v.children && v.children.length > 0
                ? v.children.reduce((r, i) => r && (i.isValid() || !i.required), true)
                : (v.isValid() || !v.required);
            return a && valid;
        }, true);
    }

    /** Mapping for form properties */
    public get form(): IBooking {
        return this.formToBooking();
    }

    public get booking(): IBooking {
        return this.model.booking || {} as any;
    }

    /** List of the selected rooms for the booking */
    public get spaces(): IRoom[] {
        return this.model.booking.room_list || [];
    }

    /** Display string for the names of the selected rooms */
    public get space_list(): string {
        return this.spaces.map(i => i.name).join(', ');
    }

    /** Number of attendees expected to turn up to the meeting */
    public get expected(): { [id: string]: number } {
        const expected = this.model.booking.expected_attendees || {};
        return expected;
    }

    /** Number of attendees expected to turn up to the meeting */
    public get equipment_notes(): { [id: string]: string } {
        const notes = (this.model.booking.notes || []).filter(i => i.type === 'equipment');
        return notes.reduce((a, v) => { a[v.space] = v.message; return a; }, {});
    }

    /** Number of attendees expected to turn up to the meeting */
    public get space_codes(): { [id: string]: string } {
        const codes = this.booking.equipment_code || {};
        return codes;
    }

    /** List of catering orders for booking */
    public get catering(): { [id: string]: ICateringOrder } {
        const catering = this.booking.catering || {};
        for (const id in catering) {
            if (catering[id]) {
                const order = catering[id];
                const space = this.service.Rooms.item(id);
                if (space) {
                    const bld = this.service.Buildings.get(space.level.bld_id);
                    if (bld) {
                        order.symbol = bld.currency;
                    }
                }
                if (!order.symbol) {
                    order.symbol = 'USD';
                }
            }
        }
        return catering;
    }

    /** Number of attendees expected to turn up to the meeting */
    public get catering_notes(): { [id: string]: string } {
        const notes: IBookingNote[] = (this.model.booking.notes || []).filter(i => i.type === 'catering');
        notes.sort((a, b) => b.date - a.date);
        return notes.reduce((a, v) => { a[v.space] = v.message; return a; }, {});
    }

    /** Display string for the booking period */
    public get time(): string {
        const bkn = this.booking
        return bkn.all_day
            ? 'All day'
            : (bkn.duration > 24 * 60
                ? '1 Week'
                : bkn.display.time);
    }

    /** Total number of catering items in booking */
    public get item_count(): number {
        return Object.keys(this.catering)
            .reduce((a, v) => { a.push(this.catering[v]); return a; }, [])
            .reduce((c, i) => c + ((i.items || []).reduce((v, j) => v + (j.amount || 0), 0) || 0), 0);
    }

    public init() {
        if (!this.service || !this.service.Settings.setup) {
            return setTimeout(() => this.init(), 500);
        }
        this.model.form = {};
        this.model.error = {};
        this.model.group = 0;
        this.model.edit = false;
        this.model.attendeescount = this.model.booking.attendees.length;
        this.model.settings = this.service.Settings.get('app.booking') || {};
        this.model.user = this.service.Users.current();
        if (this.model.booking) {
            this.checkSoon();
            if (this.model.booking.organiser) {
                this.model.organiser = typeof this.model.booking.organiser === 'string' ? this.model.booking.organiser : this.model.booking.organiser.email;
                this.model.is_organiser = this.model.booking.organiser && (this.model.organiser === this.model.user.email);
            }


            this.model.check_soon = setInterval(() => this.checkSoon(), 12 * 1000);
        }

        this.model.show_attendees = false;
        const user = this.model.user;
        if (user.delegates && user.delegates.length > 0) {
            this.is_delegate = !!user.delegates.find(i => i === this.model.organiser);
            this.delegate = this.is_delegate
                ? this.model.organiser
                : (user.delegate || []).find(i => !!this.model.booking.attendees.find(j => i === j.email));
        }

        if (!this.model.last_meeting_started) {
            this.model.last_meeting_started = 0;
        }
        this.initForm();
        this.updateDisplay();
        this.resize();
        this.updateCheckinState();
        this.interval('checkin_state', () => this.updateCheckinState(), 5 * 1000);
    }

    /** Whether the current user it the booking host/organiser */
    public get is_organiser() {
        return this.model.booking && this.model.booking.is_organiser;
    }

    public get description(): string {
        const booking: IBooking = this.model.booking || { notes: [] } as any;
        return booking.description ||(booking.notes.find(i => i.type === 'description') || { message: '' }).message;
    }

    public ngOnDestroy() {
        if (this.model.check_soon) {
            clearInterval(this.model.check_soon);
            this.model.check_soon = null;
        }
    }

    public initForm() {
        this.form_fields = generateBookingFormMetadata(
            this.model.booking,
            this.service.Settings.get('app.booking.fields'),
            this.service
        );

        const catering = this.form_fields.find(i => i.key === 'needs_catering');
        if (catering) {
            catering.setHide(true);
        }
        const host = this.form_fields.find(i => i.key === 'host');
        if (host) {
            (host.metadata = { service: this.service.Bookings, options: this.getHostOptions() });
            host.setValue(host.getValue());
        }
        const equipment = this.form_fields.find(i => i.key === 'equipment');
        if (equipment) {
            equipment.setValue(JSON.parse(JSON.stringify(this.equipment_notes)));
        }
        this.form_fields.unshift(new ADynamicFormField({
            key: 'room',
            label: 'Spaces',
            type: 'action',
            hide: false,
            format: formatSpaces,
            value: this.model.booking.room_list || [this.model.booking.room] || []
        }));
        const empty = { control: { value: true }, children: [] };
        const time = (this.form_fields.find(i => i.key === 'time_group') || empty);
        const space = (this.form_fields.find(i => i.key === 'room') || empty) as ADynamicFormField
        this.subs.obs.space = space.action.subscribe(() => this.changeSpaces(space));
        this.subs.obs.duration = (time as ADynamicFormField).children[1].control.valueChanges.subscribe((value) => {
            (time as ADynamicFormField).children[0].setDisabled(value > 450);
        });
    }

    /** Selected duration for the booking */
    public get duration(): number {
        let field: any = this.form_fields.find(i => i.key === 'duration');
        if (field) {
            return field.control.value;
        }
        field = this.form_fields.find(i => i.key === 'time_group');
        if (field) {
            return field.control.controls.duration.value;
        }
        return 60;
    }

    /**
     * Change the selected spaces
     * @param field Spaces field
     */
    public changeSpaces(field: ADynamicFormField) {
        const date = this.form.date;
        const duration = this.duration;
        this.service.Overlay.openModal('select-room', {
            data: {
                spaces: field.getValue(),
                date,
                duration,
                host: this.form_fields.find(i => i.key === 'host').getValue(),
                multi: true
            }
        }, (event) => {
            if (event.type === 'Select') {
                const list = event.data.select_list || [event.data.selected];
                list.forEach(rm => {
                    const item = this.service.Rooms.item(rm.id) || rm;
                    const bld: any = this.service.Buildings.list().find(i => i.id === item.level.bld_id) || {};
                    const rules = rulesForSpace({
                        user: this.service.Users.current(),
                        space: item,
                        time: date,
                        duration: duration || 60,
                        rules: bld.booking_rules
                    });
                    // Check booking rules
                    if (rules) {
                        item.book_type = rules.auto_approve ? 'Book' : 'Request';
                    }
                });
                field.setValue(list);
            }
            event.close();
        });
    }

    /**
     * Open modal for confirming booking details
     */
    private confirmBooking() {
        this.timeout(
            'confirm-booking',
            () => {
                const fields: any = this.formToBooking();
                fields.no_edit = true;
                fields.id = this.booking.id;
                this.service.Overlay.openModal(
                    'confirm-booking',
                    {
                        cmp: BookingDetailsModalComponent,
                        data: fields
                    },
                    event => {
                        if (event.type === 'finish') {
                            // Booking completed successfully
                            event.close();
                        } else if (event.type === 'close') {
                            event.close();
                        }
                    }
                );
            },
            100
        );
    }

    public delete() {
        if (this.service) {
            this.model.loading = true;
            this.model.processing = true;
            this.model.deleting = true;
            const query: any = this.delegate ? { host: this.delegate, acting_as: this.delegate } : {};
            query.room_ids = this.model.booking.room_list.map(i => i.id).join(',');
            this.service.Bookings.delete(
                this.model.booking.id,
                query
            ).then(() => {
                this.fn.event('delete');
                this.fn.close();
                // this.fn.event('Success');

                this.model.visitor_list = [];
                // setTimeout(() => this.fn.close(), 3000);
            }, (err) => {
                this.model.loading = false;
                this.model.deleting = false;
                if (err !== 'User cancelled') {
                    this.service.error(err);
                }
            });
        }
    }

    public decline() {
        if (this.service) {
            this.model.loading = true;
            this.model.processing = true;
            this.model.deleting = true;
            this.service.Bookings.decline(
                this.model.booking.id,
                this.model.as_delegate ? {
                    host: this.model.organiser,
                    acting_as: this.model.delegate,
                    decline: true
                } : { decline: true }
            ).then(() => {
                this.model.processing = false;
                this.fn.event('Success');
                this.model.visitor_list = [];
                setTimeout(() => this.fn.close(), 3000);
            }, (err) => {
                this.model.loading = false;
                this.model.deleting = false;
                if (err !== 'You cant decline that meeting as you are not the host') { this.service.error(err); }
            });
        }


    }

    public nextBooking() {

        // this.changeBookingDetails.emit('next');
        this.fn.event('next');
        this.fn.close();
        // this.event('next');

    }

    public previousBooking() {
        // this.changeBookingDetails.emit('previous');
        this.fn.event('previous');
        this.fn.close();
    }

    public manageAttendees() {
        this.service.Overlay.openModal('attendees', { data: { attendees: this.model.form.attendees } })
            .then((inst: any) => inst.subscribe((event: any) => {
                if (event.type === 'Change') {
                    this.model.form.attendees = event.data.form.attendees || [];
                    if (!this.model.error) { this.model.error = {}; }
                    this.model.error.attendees = null;
                    this.model.form.room = this.model.form.room;
                    this.model.form = JSON.parse(JSON.stringify(this.model.form));
                }
                event.close();
            }));
    }

    public event(e) {
        this.model.booking = e;
        if (e === 'Attendees') {
            if (this.model.form.attendees) {
                for (const item of this.model.form.attendees) {
                    item.select = true;
                }
            }
            this.manageAttendees();
        }
    }

    public edit() {
        if (localStorage) {
            localStorage.setItem('STAFF.booking_form', JSON.stringify({
                ...this.booking,
                room: this.booking.room_list.map(i => ({ ...i, boookings: [] }))
            }));
        }
        this.service.navigate(['book']);
        this.fn.close();
    }

    public confirmStart(checkin: boolean = false) {
        const title = checkin ? `Check in to meeting?` : 'Do you wish to start your meeting?';
        const msg = checkin ? 'check in to' : 'start';
        this.service.confirm({
            title,
            message: `If you don\'t ${msg} your meeting it will be cancelled ${this.model.timeout / 60} minutes after the start time.`,
            icon: 'play_arrow',
            accept: 'Ok',
            cancel: true,
        }, (event) => {
            if (event.type === 'Accept') {
                // this.startMeeting();
            }
            event.close();
        });
    }

    public editCatering() {
        if (localStorage) {
            localStorage.setItem('STAFF.booking_form', JSON.stringify({
                ...this.booking,
                room: this.booking.room_list.map(i => ({ ...i, boookings: [] }))
            }));
        }
        this.service.navigate(['book', 'main', 'catering'], { back: 'schedule' });
        this.fn.close();
    }

    public editRequirements() {
        this.initForm();
        this.addEquipment().then(() => {
            this.confirmBooking();
        });
    }

    /**
     * Show modal to user for adding equipment details to a booking
     */
    private addEquipment() {
        return new Promise((resolve) => {
            const empty = { control: { value: '' } };
            const notes = this.form_fields.find(i => i.key === 'equipment');
            const expected_attendees = this.form_fields.find(i => i.key === 'expected_attendees');
            const spaces = this.form_fields.find(i => i.key === 'room');
            const cost_code = this.form_fields.find(i => i.key === 'equipment_code');
            this.service.Overlay.openModal(
                'booking-equipment',
                {
                    cmp: BookingEquipmentDetailsModalComponent,
                    data: {
                        spaces: (spaces || empty).control.value || [...this.booking.room_list],
                        expected_attendees: (expected_attendees || empty).control.value || {},
                        notes: (notes || empty).control.value,
                        cost_code: (cost_code || empty).control.value
                    }
                },
                event => {
                    if (event.type === 'finish') {
                        if (notes) {
                            notes.setValue(event.data.notes);
                        }
                        if (expected_attendees) {
                            expected_attendees.setValue(event.data.expected_attendees || {});
                        }
                        if (cost_code) {
                            cost_code.setValue(event.data.cost_code);
                        }
                    }
                    resolve();
                    event.close();
                }
            );
        });
    }

    public extendMeeting() {
        this.model.loading = true;
        this.model.processing = true;
        this.model.form = {
            ...this.model.booking
        };
        this.service.Overlay.openModal('time-extension', { data: {
            room_list: this.model.booking.room_list.map(i => i.id),
            date: this.model.booking.date,
            duration: this.model.booking.duration
        } }, (event: any) => {
            if (event.type === 'Extend') {
                this.extend_by = event.data.extend_by;
                this.service.Bookings.update(this.booking.id, {
                    ...this.booking,
                    duration: this.booking.duration + this.extend_by
                }).then(() => {
                    this.close();
                    this.service.success('Successfully extended meeting.');
                }, () => {
                    this.service.error('Error extending meeting.');
                    this.model.loading = false;
                    this.model.processing = false;
                })
            } else if (event.type === 'close') {
                this.model.loading = false;
                this.model.processing = false;
            }
            event.close();
        });
    }


    public confirm() {
        this.model.checkin = 'done';

        //call the start meeting method
        this.startMeeting();

    }

    /**
     * Update the current state of checkin for the meeting
     */
    public updateCheckinState() {
        this.checkin = 'unavailable';
        if (!this.model.booking || this.model.booking.available === false
            || (!this.is_organiser && !this.is_delegate) || !this.model.timeout) {
            return;
        }
        const now = moment();
        const booking_end = moment(this.model.booking.date).add(this.model.booking.duration, 'm');
        // Check in is unavailable if the meeting has ended
        if (now.isAfter(booking_end, 'm')) {
            return;
        }
        const pending_start = moment(this.model.booking.date).subtract(this.model.timeout || 900, 's').startOf('m');
        const pending_end = moment(this.model.booking.date).add(this.model.cancel_timeout || this.model.timeout || 900, 's').startOf('m');
        if (this.model.booking.date <= this.model.last_meeting_started) {
            this.checkin = 'started';
        } else if (now.isAfter(pending_start, 's') && now.isBefore(pending_end, 's')) {
            this.checkin = 'available';
        } else if (now.isAfter(pending_end, 's')) {
            this.checkin = 'expired';
        }
    }

    public startMeeting() {
        this.module = this.service.Bindings.module(this.model.booking.room.id, 'Bookings_1');
        if (this.model.booking.room && this.model.booking.date && this.module) {
            this.module.exec('start_meeting', this.model.booking.date);
        }
    }

    public duplicate() {
        this.model.duplicate = true;
        const booking = this.model.booking;
        const attendees = booking.attendees;
        const room = booking.room_list;
        const now = moment();
        let date = moment(booking.date);
        if (date.isBefore(now, 'm')){
            date = moment().startOf('m');
            date.minute(Math.ceil(date.minute() / 5) * 5);
        }
        // Make copy of booking data
        this.model.form = { ...this.model.booking, date: date.valueOf() };
        this.model.form.host =
            this.service.Users.item(this.model.booking.organiser.email)
            || this.service.Users.current();
        this.model.form.needs_catering = this.item_count > 0;
        this.model.form.catering = null;
        this.model.form.start = moment(this.model.form.date).format('HH:mm');
        delete this.model.form.room;
        this.model.form.attendees = attendees && attendees.length > 1 ? [...(attendees.filter(i => i.email !== booking.organiser.email))] : [];
        const room_list = room instanceof Array ? room.map(i => ({ ...i, bookings: [] })) : [{ ...room }];
        for (const room of room_list) {
            const bld: any = this.service.Buildings.list().find(i => i.id === room.level.bld_id) || {};
            const rules = rulesForSpace({
                user: this.service.Users.current(),
                space: room,
                time: this.model.form.date,
                duration: this.model.form.duration || 60,
                rules: bld.booking_rules
            });
            room.book_type = rules.auto_approve ? 'Book' : 'Request';
        }
        this.model.form.room = room_list;
        delete this.model.form.room_list;
        localStorage.setItem('STAFF.booking_form', JSON.stringify(this.model.form));
        this.model.duplicate = false;
        this.timeout('duplicate', () => this.service.navigate(['book'], { back: 'schedule' }));
        this.fn.close();
    }

    public control() {
        if (this.model.booking && this.model.booking.room && this.model.booking.room.support_url) {
            window.open(this.model.booking.room.support_url, 'blank_');
        }
    }

    public checkSoon() {
        const now = moment();
        const start = moment(this.model.booking.date);
        this.model.is_past = now.isAfter(start);
        // if (this.model.is_past) {
        //     this.model.edit = false;
        // }
        const end = moment(start).add(this.model.booking.duration, 'm');
        this.model.soon = now.isBetween(start.add(-(this.model.settings.pre_control || 15), 'm'), end, 'm', '[]');
        const cancel = moment(start).add(-(this.service.Settings.get('app.booking.lock_cancel') || 0), 'm');
        this.model.can_cancel = now.isBefore(cancel, 'm');
    }

    public locate(id: string) {
        if (id && this.service.Rooms.item(id)) {
            this.service.Overlay.openModal('view-room', {
                data: {
                    room: this.service.Rooms.item(id),
                }
            }).then((inst: any) => inst.subscribe((event) => event.close()));
        }
    }

    public get can_edit() {
        const user = this.service.Users.current();
        const booking: IBooking = this.model.booking;
        const now = dayjs().startOf('m');
        const date = dayjs(booking.date).add(booking.duration, 'm');
        this.is_delegate = user.delegates && user.delegates.length > 0 && !!user.delegates.find(i => i === this.model.organiser);
        return (this.model.is_organiser || this.is_delegate)
            && now.isBefore(date, 'm')
            && !this.model.edit
    }

    public updateDisplay() {
        if (!this.model.form) { this.model.form = {}; }
        if (!this.model.form.date) { this.model.form.date = moment().seconds(0).milliseconds(0).valueOf(); }
        this.model.display = {};
        const date = moment(this.model.form.date);
        this.model.display.date = date.format('dddd, Do MMMM YYYY');
        this.model.display.time = date.format('h:mm a');
    }

    public resize() {
        super.resize();
        this.model.vh = Math.max(document.documentElement.clientHeight, window.innerHeight || 0);
    }

    /**
     * Get list of users available to select as the meeting host
     */
    private getHostOptions(): IUser[] {
        const user = this.service.Users.current();
        const list: IUser[] = [user];
        if (user.delegates) {
            for (const email of user.delegates) {
                const delegate = this.service.Users.item(email);
                if (delegate) {
                    list.push(delegate);
                } else {
                    this.timeout(`load_user|${email}`, () => this.service.Users.show(email, { update: true }).then(() => {
                        this.timeout('update_host_options', () => {
                            const host = this.form_fields.find(i => i.key === 'host');
                            if (host) {
                                (host.metadata = { service: this.service.Bookings, options: this.getHostOptions() });
                                host.setValue(host.getValue());
                            }
                        });
                    }));
                }
            }
        }
        return list;
    }

    /**
     * Convert form field data to a Booking object
     */
    private formToBooking(): IBooking {
        return this.form_fields.reduce((v, i) => {
            if (i.children && i.children.length) {
                i.children.forEach(j => v[j.key] = j.control.value);
            } else {
                v[i.key] = i.control.value;
            }
            return v;
        }, {}) as IBooking;
    }

}

